<template>
  <div class="home">

    <div v-if="sectionsOn" class="home-section-division">
      <div class="home-sections" v-bind:style="sectionStyle" v-for="(subsection, index) in sections" v-bind:key="index">
        <section-line 
          v-for="(section, subindex) in subsection" 
          v-bind:key="subindex" 
          v-bind:section="section" 
          v-bind:openSection="openSection"
        />
      </div>
    </div>


    <section-modal 
      v-if="section.on"
      v-bind:data="section.data"
      v-bind:closeSection="closeSection"
    />

    <app-loading 
      v-if="loading"
    />

    <app-add 
      v-if="add.on" 
      v-bind:add="showAdd" 
      v-bind:addSection="addSection" 
      v-bind:addNpc="addNpc" 
    />

    <add-section v-if="add.section" v-bind:add="addSection" v-bind:refresh="getSections" v-bind:loading="setLoading" />
    <add-npc     v-if="add.npc"     v-bind:add="addNpc"     v-bind:refresh="getSections" v-bind:loading="setLoading" />

    <img v-if="openToAdd" class="img-add" v-on:click="showAdd" src="data:image/png;base64, " >
  </div>
</template>

<script>
import Axios from 'axios';
import SectionLine from '../components/SectionLine';
import SectionModal from '../components/SectionModal';
import AppAdd from '../components/Add';
import AddSection from '../components/AddSection';
import AddNpc from '../components/AddNpc';
import Loading from '../components/Loading';

export default {
  name: 'Home',
  mounted() {
    this.getSections(true, true);
    window.onresize = (event) => this.onresize(event);
    window.onkeydown = (event = new KeyboardEvent) => {
      if (event.keyCode == 27) this.closeAll();
    };
  },
  props: {
    shared: {
      type: Object,
      required: true
    }
  },
  components: {
    SectionLine,
    SectionModal,
    AppLoading: Loading,
    AppAdd,
    AddSection,
    AddNpc
  },
  data: () => ({
    resizeCount: 0,
    sectionsOn: false,
    sections: [],
    section: {
      on: false,
      data: null
    },
    add: {
      on: false,
      section: false,
      npc: false
    },
    loading: false,
    sectionStyle: {
      width: '0px',
      height: '155px'
    },
    pageOffSet: {
      width: 0,
      height: 0
    },
    openToAdd: false
  }),
  methods: {
    closeAll() {
      this.add.section  = false;
      this.add.npc      = false;
      this.add.on       = false;
      this.section.on   = false;
      this.section.data = null ;
    },
    addSection() {
      this.showAdd();
      this.add.section = !this.add.section;
    },
    addNpc() {
      this.showAdd();
      this.add.npc = !this.add.npc;
    },
    showAdd() {
      this.add.on = !this.add.on;
    },
    setLoading(value) {
      this.loading = value;
    },
    sleep(time) {
      time = (parseFloat(time) || 1) * 1000;
      return new Promise((resolve) => {
        setTimeout(() => { resolve(true); }, time);
      });
    },
    getWindowOffSet() {
      let { innerWidth, innerHeight } = window;
      return { width: innerWidth, height: innerHeight };
    },
    async onresize(event) {
      let _last = this.getWindowOffSet();
      if (this.pageOffSet.width == _last.width && this.pageOffSet.height == _last.height) return true;
      this.resizeCount++;
      await this.sleep(0.5);
      let _new  = this.getWindowOffSet(); 
      this.setLoading(true);

      this.resizeCount--;
      if (_last.width == _new.width && _last.height == _new.height) {

        if (this.pageOffSet.width != _new.width || this.pageOffSet.height != _new.height) {
          this.getSections(false, true);
          this.setPageOffset(_new);
        }

        if (this.resizeCount == 0) this.setLoading(false);
      }
    },
    setPageOffset(offset = { width: 0, height: 0 }) {
      if (offset.width > 400) this.openToAdd = true;
      this.pageOffSet.width = offset.width;
      this.pageOffSet.height= offset.height;
    },
    setPartitionSectionInPage(sections = [], offset = { width: 0, height: 0 }) {
      let _sections = [];
      let subsections = [];
      let len = 0, count = 1;
      let width = 250;

      let division = offset.width / width;
      let range    = 0.25;
      var total = Math.floor(division);

      if (division + 0.25 > total + 1) {
        total++;
      }

      if (total == 0) total = 1;

      while(true) {
        let _section = sections[len];

        if (!_section) {
          if (subsections.length) _sections.push(subsections);
          break;
        };

        if (total == 1) {
          _sections.push([_section]);
          len++;
          continue;
        }
        
        if (count == total) {
          _sections.push(subsections);
          subsections = [];
          count = 1;
        }
        else {
          subsections.push(_section);
          count++;
          len++;
        }
      }

      this.sectionStyle.width = `${(total * width) + 10}px`;

      if (this.pageOffSet.width == 0 && this.pageOffSet.height == 0) this.setPageOffset(offset);
      return _sections;
    },
    async getSections(initial, reload) {
      if (initial) this.loading = true;
      if (reload)  this.sectionsOn = false;
      window.document.body.style.cursor = 'progress';
      let sections = [];
      try {
        let { data } = await Axios({
          url: `/section`,
          method: 'GET'
        });

        sections = data;
      } catch (error) { }

      this.sections = this.setPartitionSectionInPage(sections, this.getWindowOffSet());
      if (reload)  this.sectionsOn = true;
      if (initial) this.loading = false;
      window.document.body.style.cursor = 'default';
    },
    setSectionData(value) {
      this.section.data = value;
    },
    setSectionOn(value) {
      this.section.on = value;
    },
    openSection(section) {
      this.setSectionData(section);
      this.setSectionOn(true);
    },
    closeSection() {
      this.setSectionOn(false);
      this.setSectionData(null);
    }
  }
}
</script>

<style>

@media screen and (max-width: 500px){
  .home {
    width: 100%;
    height: 100%;
    margin: auto;
  }
}
@media screen and (min-width: 501px){
  .home {
    width: 100%;
    height: 100%;
    margin: auto;
  }
}

.home-section-division {
  width: 100%;
  height: 100%;
}
.home-sections {
  margin: 0px auto;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  display: flex;
}

.img-add {
  position: fixed;
  width: 50px;
  height: 50px;
  bottom: 20px;
  right: 20px;
  cursor: pointer;
}

.img-add:hover {
  opacity: 0.6;
}
</style>